import React, { useState, useContext } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";

import { Form, Input, Col, Button, Space } from "antd";
import { BASE_URL } from "../constants/api";
import { AuthContext } from "../contexts/auth.context";

const BrandEdit = () => {
  const [form] = Form.useForm();
  const [fields, setFields] = useState([{ name: ["name"] }]);
  const { logout } = useContext(AuthContext);

  const submit = () => {
    let data = fields.reduce((data, item) => {
      data[item.name[0]] = item.value;
      return data;
    }, {});

    axios
      .post(`${BASE_URL}/api/brands`, data)
      .then(() => {
        toast.success("Thêm thương hiệu thành công");
      })
      .catch((e) => {
        if (e.response.status === 401 || e.response.status === 403) {
          toast.error("Phiên làm việc hết hạn, vui lòng đăng nhập lại");
          logout();
        } else {
          toast.error("Thêm thương hiệu thất bại");
        }
      });
  };

  const onReset = () => {
    form.resetFields();
  };

  return (
    <div>
      <Form
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 24 }}
        form={form}
        fields={fields}
        onFieldsChange={(changedFields, allFields) => {
          setFields(allFields);
        }}
        onFinish={submit}
        layout="vertical"
      >
        <Col span={12}>
          <Form.Item label="Brand name" name="name">
            <Input />
          </Form.Item>
          <Space size="small">
            <Button type="primary" htmlType="submit">
              Thêm
            </Button>
            <Button type="primary" htmlType="button" onClick={onReset}>
              Hủy
            </Button>
            <Button type="primary" htmlType="button">
              <Link to="/brands">Quay lại</Link>
            </Button>
          </Space>
        </Col>
      </Form>
    </div>
  );
};

export default BrandEdit;
