import React, { useContext } from "react";
import { Modal } from "antd";
import axios from "axios";
import { toast } from "react-toastify";

import { BASE_URL, PRODUCT_API } from "../../../../constants/api";
import { AuthContext } from "../../../../contexts/auth.context";

function DeleteModal({ modalOpen, setModalOpen, selected }) {
  const { logout } = useContext(AuthContext);

  const stopBusiness = (id) => {
    setModalOpen(false);
    axios
      .post(BASE_URL + PRODUCT_API + `/${id}/stop-business`)
      .then(() => {
        toast.info("Sản phẩm đã chuyển sang trạng thái ngừng kinh doanh");
        window.location.reload();
      })
      .catch((e) => {
        if (e.response.status === 401 || e.response.status === 403) {
          toast.error("Phiên làm việc đã hết hạn, vui lòng đăng nhập lại");
          logout();
        } else {
          toast.error("Cập nhật sản phẩm thất bại");
        }
      });
  };

  const selling = (id) => {
    setModalOpen(false);
    axios
      .post(BASE_URL + PRODUCT_API + `/${id}/selling`)
      .then(() => {
        toast.info("Sản phẩm đã chuyển sang trạng thái mở bán");
        window.location.reload();
      })
      .catch((e) => {
        if (e.response.status === 401 || e.response.status === 403) {
          toast.error("Phiên làm việc đã hết hạn, vui lòng đăng nhập lại");
          logout();
        } else {
          toast.error("Cập nhật sản phẩm thất bại");
        }
      });
  };

  return (
    <div>
      <Modal
        title={
          selected.status === "Selling"
            ? "Ngừng kinh doanh sản phẩm"
            : "Mở bán sản phẩm"
        }
        style={{ top: 0 }}
        visible={modalOpen}
        onOk={() =>
          selected.status === "Selling"
            ? stopBusiness(selected.id)
            : selling(selected.id)
        }
        onCancel={() => setModalOpen(false)}
      >
        <div>
          Bạn có muốn{" "}
          {selected.status === "Selling" ? "ngừng kinh doanh" : "mở bán"}:{" "}
          {selected.name}?
        </div>
      </Modal>
    </div>
  );
}

export default DeleteModal;
