import React from "react";
import { Layout } from "antd";

import UserItem from "../UserItem";

const { Header } = Layout;

const SiteHeader = () => {
  return (
    <Header
      style={{
        padding: "0 16px",
        backgroundColor: "#fff",
        display: "flex",
        justifyContent: "flex-end"
      }}
    >
      <UserItem />
    </Header>
  );
};

export default SiteHeader;
