import React, { useState } from "react";
import { Upload, Modal } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import axios from "axios";

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
}

function UploadImage({ changeImage }) {
  const [state, setState] = useState({
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
    fileList: []
  });

  const handleCancel = () => {
    setState({ ...state, previewVisible: false });
  };

  const handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    setState({
      ...state,
      previewImage: file.preview,
      previewVisible: true,
      previewTitle: file.response.url || file.name
    });
  };

  const handleChange = ({ fileList }) => {
    setState({ ...state, fileList });

    if (fileList[0]) {
      const file = fileList[0];
      const fileName = file.response ? file.response.url : file.name;
      changeImage(fileName);
    } else {
      changeImage("");
    }
  };

  const customRequest = (options) => {
    const data = new FormData();
    data.append("image", options.file);
    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    axios
      .post(options.action, data, config)
      .then((res) => {
        options.onSuccess(res.data, options.file);
      })
      .catch(console.log);
  };

  const uploadButton = (
    <div>
      <PlusOutlined />
      <div className="ant-upload-text">Upload</div>
    </div>
  );
  return (
    <div className="clearfix" style={{ display: "flex" }}>
      <Upload
        action="/api/upload"
        listType="picture-card"
        fileList={state.fileList}
        onPreview={handlePreview}
        onChange={handleChange}
        multiple={false}
        accept="image/*"
        customRequest={customRequest}
      >
        {state.fileList.length >= 1 ? null : uploadButton}
      </Upload>
      <Modal
        visible={state.previewVisible}
        title={state.previewTitle}
        footer={null}
        onCancel={handleCancel}
      >
        <img alt="preview" style={{ width: "100%" }} src={state.previewImage} />
      </Modal>
    </div>
  );
}

export default UploadImage;
