import styled from "styled-components";

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  max-width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
  position: relative;

  img {
    position: absolute;
    top: 0;
    left: 50%;
    border: 3px solid #fff;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }

  h2 {
    margin: 40px 0 20px;
    color: #fff;
    font-weight: 300;
    text-transform: uppercase;
  }

  input {
    margin-bottom: 15px;
    padding: 10px 14px;
    width: 240px;
    background-color: rgba(194, 194, 194, 0.15);
    color: #fff;
    border: none;
    border-radius: 17.5px;
    outline: none;
  }

  button {
    margin-top: 10px;
    padding: 8px 16px;
    width: 100%;
    background-color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
`;

export { LoginForm as LoginFormStyled };
