import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Table, Tag, Space, Button } from "antd";
import axios from "axios";

import { BASE_URL, PRODUCT_API } from "../constants/api";
import { toVND } from "../helpers/formatter";

import ProductDeleteModal from "../components/Product/Delete/Modal";

const columns = (openModal) => {
  return [
    {
      title: "Id",
      dataIndex: "id",
      key: "id",
      render: (id) => <Link to={`/products/${id}/edit`}>{id}</Link>
    },
    {
      title: "Image",
      dataIndex: "image",
      key: "image",
      render: (image) => (
        <div style={{ width: 50, height: 50, display: "flex" }}>
          <img
            style={{ width: "100%" }}
            src={image ? `/images/${image}` : ""}
            alt=""
          />
        </div>
      )
    },
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
      sorter: (a, b) => b.name.localeCompare(a.name),
      sortDirections: ["descend", "ascend"]
    },
    {
      title: "Price",
      dataIndex: "price",
      key: "price",
      render: (price) => toVND(price),
      sorter: (a, b) => a.price - b.price,
      sortDirections: ["descend", "ascend"]
    },
    {
      title: "Original Price",
      dataIndex: "originalPrice",
      key: "originalPrice",
      render: (originalPrice) => toVND(originalPrice),
      sorter: (a, b) => a.originalPrice - b.originalPrice,
      sortDirections: ["descend", "ascend"]
    },
    {
      title: "Quantity",
      dataIndex: "quantity",
      key: "quantity",
      sorter: (a, b) => a.quantity - b.quantity,
      sortDirections: ["descend", "ascend"]
    },
    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      render: (status) => (
        <Tag color={status === "Selling" ? "green" : "red"}>{status}</Tag>
      ),
      sorter: (a, b) => a.status.length - b.status.length,
      sortDirections: ["descend", "ascend"]
    },
    {
      title: "Action",
      dataIndex: "id",
      key: "action",
      render: (id, record) => (
        <Space size="middle">
          <Link to={`/products/${id}/edit`}>Edit</Link>
          <Button type="link" onClick={() => openModal(record)}>
            {record.status === "Selling" ? "Stop business" : "Selling"}
          </Button>
        </Space>
      )
    }
  ];
};

const Products = () => {
  const [dataSource, setDataSource] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [selected, setSelected] = useState({});

  const openModal = (record) => {
    setSelected(record);
    setModalOpen(true);
  };

  useEffect(() => {
    axios
      .get(BASE_URL + PRODUCT_API + "?all=true")
      .then((res) => res.data)
      .then((res) =>
        setDataSource(res.data.map((item, index) => ({ ...item, key: index })))
      );
  }, []);

  return (
    <div>
      <Table
        dataSource={dataSource}
        columns={columns(openModal)}
        pagination={{
          defaultPageSize: 4,
          showSizeChanger: true,
          pageSizeOptions: ["2", "4", "8", "16"]
        }}
      />
      <ProductDeleteModal
        modalOpen={modalOpen}
        setModalOpen={setModalOpen}
        selected={selected}
      />
    </div>
  );
};

export default Products;
