import React, { useState, useEffect, useContext } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";
import {
  Form,
  Row,
  Col,
  Input,
  InputNumber,
  Button,
  Space,
  Select
} from "antd";

import UploadImage from "../../../UploadImage";

import { BASE_URL, BRAND_API, PRODUCT_API } from "../../../../constants/api";
import { AuthContext } from "../../../../contexts/auth.context";

const { TextArea } = Input;
const { Option } = Select;

const ProductEditForm = ({ initialValues }) => {
  const [form] = Form.useForm();
  const [fields, setFields] = useState([
    { name: ["image"] },
    { name: ["name"] },
    { name: ["brandId"] },
    { name: ["price"] },
    { name: ["originalPrice"] },
    { name: ["quantity"] },
    { name: ["screenSize"] },
    { name: ["screenType"] },
    { name: ["resolution"] },
    { name: ["os"] },
    { name: ["frontCamera"] },
    { name: ["backCamera"] },
    { name: ["cpu"] },
    { name: ["ram"] },
    { name: ["rom"] },
    { name: ["sim"] },
    { name: ["pin"] },
    { name: ["status"] },
    { name: ["highlighFeature"] }
  ]);
  const [brands, setBrands] = useState([]);
  const { id } = useParams();
  const { logout } = useContext(AuthContext);

  useEffect(() => {
    axios
      .get(BASE_URL + BRAND_API)
      .then((res) => res.data)
      .then((res) => setBrands(res.data));
  }, []);

  const submit = () => {
    let data = fields.reduce((data, item) => {
      data[item.name[0]] = item.value;
      return data;
    }, {});

    axios
      .put(BASE_URL + PRODUCT_API + `/${id}`, data)
      .then(() => {
        toast.success("Cập nhật sản phẩm thành công");
      })
      .catch((e) => {
        if (e.response.status === 401 || e.response.status === 403) {
          toast.error("Phiên làm việc hết hạn, vui lòng đăng nhập lại");
          logout();
        } else {
          toast.error("Cập nhật sản phẩm thất bại");
        }
      });
  };

  const onReset = () => {
    form.resetFields();
  };

  const changeImage = (imageName) => {
    setFields(
      fields.map((item) => {
        if (item.name[0] === "image") {
          return { ...item, value: imageName };
        } else {
          return item;
        }
      })
    );
  };

  return (
    <div>
      <Form
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 24 }}
        form={form}
        fields={fields}
        initialValues={initialValues}
        onFieldsChange={(changedFields, allFields) => {
          setFields(allFields);
        }}
        onFinish={submit}
        layout="horizontal"
      >
        <Row>
          <Col span={12}>
            <Form.Item label="Product Name" name="name">
              <Input />
            </Form.Item>
            <Form.Item label="Brand" name="brandId">
              <Select>
                {brands.map((brand) => (
                  <Option value={brand.id} key={brand}>
                    {brand.name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
            <Form.Item label="Price" name="price">
              <InputNumber min={1} style={{ width: "100%" }} />
            </Form.Item>
            <Form.Item label="Original Price" name="originalPrice">
              <InputNumber min={1} style={{ width: "100%" }} />
            </Form.Item>
            <Form.Item label="Quantity" name="quantity">
              <InputNumber min={0} style={{ width: "100%" }} />
            </Form.Item>
            <Form.Item label="Loại màn hình" name="screenType">
              <Input />
            </Form.Item>
            <Form.Item label="Kích thước màn hình" name="screenSize">
              <Input />
            </Form.Item>
            <Form.Item label="Độ phân giải" name="resolution">
              <Input />
            </Form.Item>
            <Form.Item label="Hệ điều hành" name="os">
              <Input />
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Camera trước" name="frontCamera">
              <Input />
            </Form.Item>
            <Form.Item label="Camera sau" name="backCamera">
              <Input />
            </Form.Item>
            <Form.Item label="CPU" name="cpu">
              <Input />
            </Form.Item>
            <Form.Item label="RAM" name="ram">
              <Input />
            </Form.Item>
            <Form.Item label="Bộ nhớ" name="rom">
              <Input />
            </Form.Item>
            <Form.Item label="SIM" name="sim">
              <Input />
            </Form.Item>
            <Form.Item label="Pin" name="pin">
              <Input />
            </Form.Item>
            <Form.Item label="Trạng thái" name="status">
              <Select style={{ width: "100%" }}>
                <Option value="Selling">Selling</Option>
                <Option value="Stop Business">Stop Business</Option>
              </Select>
            </Form.Item>
            <Form.Item label="Tính năng nổi bật" name="highlightFeature">
              <TextArea rows={1} autoSize />
            </Form.Item>
            <Form.Item label="Hình ảnh" name="image">
              <UploadImage
                changeImage={changeImage}
                initialValue={initialValues.image}
              />
            </Form.Item>
          </Col>
        </Row>
        <Row justify="center">
          <Col>
            <Space size="small">
              <Button type="primary" htmlType="submit">
                Cập nhật
              </Button>
              <Button type="primary" htmlType="button" onClick={onReset}>
                Hủy
              </Button>
              <Button type="primary" htmlType="button">
                <Link to="/products">Quay lại</Link>
              </Button>
            </Space>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default ProductEditForm;
