import React, { useState, useContext, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";

import { Form, Input, Col, Button, Space } from "antd";
import { BASE_URL } from "../constants/api";
import { AuthContext } from "../contexts/auth.context";

const BrandEdit = () => {
  const [form] = Form.useForm();
  const [fields, setFields] = useState([{ name: ["name"] }]);
  const { id } = useParams();
  const { logout } = useContext(AuthContext);
  const [brand, setBrand] = useState({});

  useEffect(() => {
    axios
      .get(`${BASE_URL}/api/brands/${id}`)
      .then((res) => res.data)
      .then((res) => setBrand(res.data));
  }, [id]);

  const submit = () => {
    let data = fields.reduce((data, item) => {
      data[item.name[0]] = item.value;
      return data;
    }, {});

    axios
      .put(`${BASE_URL}/api/brands/${id}`, data)
      .then(() => {
        toast.success("Cập nhật thương hiệu thành công");
      })
      .catch((e) => {
        if (e.response.status === 401 || e.response.status === 403) {
          toast.error("Phiên làm việc hết hạn, vui lòng đăng nhập lại");
          logout();
        } else {
          toast.error("Cập nhật thương hiệu thất bại");
        }
      });
  };

  const onReset = () => {
    form.resetFields();
  };

  if (Object.keys(brand).length) {
    return (
      <div>
        <Form
          labelCol={{ span: 8 }}
          wrapperCol={{ span: 24 }}
          form={form}
          fields={fields}
          initialValues={brand}
          onFieldsChange={(changedFields, allFields) => {
            setFields(allFields);
          }}
          onFinish={submit}
          layout="vertical"
        >
          <Col span={12}>
            <Form.Item label="Brand name" name="name">
              <Input />
            </Form.Item>
            <Space size="small">
              <Button type="primary" htmlType="submit">
                Cập nhật
              </Button>
              <Button type="primary" htmlType="button" onClick={onReset}>
                Hủy
              </Button>
              <Button type="primary" htmlType="button">
                <Link to="/brands">Quay lại</Link>
              </Button>
            </Space>
          </Col>
        </Form>
      </div>
    );
  } else {
    return null;
  }
};

export default BrandEdit;
