import React, { useState, useEffect } from "react";
import axios from "axios";
import { toast } from "react-toastify";

import {
  BASE_URL,
  ADMIN_LOGIN_BY_TOKEN_API,
  LOGOUT_API
} from "../constants/api";

export const AuthContext = React.createContext();
export const AuthProvider = ({ children }) => {
  const initialUser = JSON.parse(localStorage.getItem("user") || "{}");
  const [user, setUser] = useState(initialUser);

  useEffect(() => {
    localStorage.setItem("user", JSON.stringify(user));
  }, [user]);

  const loginByToken = () => {
    axios.defaults.withCredentials = true;
    return axios
      .post(BASE_URL + ADMIN_LOGIN_BY_TOKEN_API)
      .then((res) => res.data)
      .then(() => {
        return true;
      })
      .catch((e) => {
        console.log(e.response);
        setUser({});
        return false;
      });
  };

  const login = (username, password, cb) => {
    axios.defaults.withCredentials = true;
    axios
      .post(`${BASE_URL}/api/auth/admin-login`, { username, password })
      .then((res) => res.data)
      .then((res) => {
        setUser(res.data);
        toast.success("Đăng nhập thành công");
        if (typeof cb === "function") {
          cb();
        }
      })
      .catch((e) => {
        toast.error("Đăng nhập thất bại");
        console.log(e.response);
      });
  };

  const logout = () => {
    setUser({});
    axios.defaults.withCredentials = true;
    return axios.post(BASE_URL + LOGOUT_API).then(true, false);
  };

  const context = { user, loginByToken, login, logout };
  return (
    <AuthContext.Provider value={context}>{children}</AuthContext.Provider>
  );
};
