import React, { useEffect, useState } from "react";
import { Row, Col } from "antd";
import { Pie } from "react-chartjs-2";
import axios from "axios";

import { BASE_URL } from "../constants/api";

const Dashboard = () => {
  const [data, setData] = useState({});

  useEffect(() => {
    axios
      .get(`${BASE_URL}/api/analysis/best-seller?limit=5`)
      .then((res) => res.data)
      .then((res) => {
        let labels = res.data.map((item) => item.productName);
        let data = res.data.map((item) => item.amount);
        let backgroundColor = [
          "rgba(255, 99, 132, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(153, 102, 255, 0.2)"
        ];
        setData({
          labels,
          datasets: [
            {
              data,
              backgroundColor
            }
          ]
        });
      });
  }, []);
  return (
    <Row>
      <Col span={24}>
        <h2>Sản phẩm bán chạy</h2>
        <Pie data={data} />
      </Col>
      <Col span={8}></Col>
      <Col span={8}></Col>
    </Row>
  );
};

export default Dashboard;
